<template>
	<div class="container my-3">
		<div class="input-group mb-3" v-if="isInput==false">
			<div class="input-group-prepend">
				<span class="input-group-text" id="basic-addon1"></span>
			</div>
			<input type="text" class="form-control" placeholder="" @click="isInput=true">
			<div class="input-group-append">
				<button class="btn btn-primary" type="button"></button>
			</div>
		</div>
		<div class="mb-4 card border-secondary" v-if="isInput!=false">
			<div class="card-item active">
				<div class="card-header">
					<div class="card-title">
						<input type="text" autofocus placeholder="" class="form-control" v-model="newtodo.Name">
					</div>
				</div>
				<div class="card-body bg-light">
					<div class="card-content">
						<div class="card-row">
							<div class="card-icon">
								<i class="far fa-calendar-alt"></i>
							</div>
							<div>
								<label for="">Time</label>
								<div class="form-inline card-form-control">
									<input type="date" class="form-control" v-model="newtodo.date">
									<span style="width:1rem"></span>
									<input type="time" class="form-control border-0" v-model="newtodo.time">
								</div>
							</div>
						</div>
						<div class="card-row">
							<div class="card-icon">
								<i class="fas fa-map-marker-alt"></i>
							</div>
							<div>
								<label for="">locate</label>
								<div class="form-inline card-form-control">
									<div class="btn-group">
										<input type="text" class="form-control border-0" name="" id="" v-model="newtodo.Add">
										<button type="button" v-if="choosable.length>0" class="btn btn dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
											<span class="sr-only">Toggle Dropdown</span>
										</button>
										<div class="dropdown-menu">
											<a class="dropdown-item" href="#" @click.prevent="newtodo.Add=item.Name" v-for="(item,i) in choosable" :key="i">{{item.Name}}</a>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div class="card-row">
							<div class="card-icon">
								<i class="far fa-comment-dots"></i>
							</div>
							<div class="card-form-control">
								<label for="">Comment</label>
								<div>
									<textarea name="" class="form-control w-100 border-0" v-model="newtodo.message"></textarea>
								</div>
							</div>
						</div>
					</div>
					<div class="card-btn-group d-flex" style="margin-top:1rem">
						<button class="btn text-danger w-50 btn-lg" @click="isInput=false">
							<i class="fas fa-times"></i> Cancel
						</button>
						<button class="btn btn-primary w-50 btn-lg" @click="addTodo">
							<i class="fas fa-plus"></i>  Task
						</button>
					</div>
				</div>
			</div>
		</div>
		<div class="card text-center">
			<div class="card-header">
				<ul class="nav nav-pills">
					<li class="nav-item">
						<a class="nav-link" :class="{active:isActive=='all'}" href="#" @click.prevent="isActive='all'"></a>
					</li>
					<li class="nav-item">
						<a class="nav-link" href="#" :class="{active:isActive=='process'}"  @click.prevent="isActive='process'"></a>
					</li>
					<li class="nav-item">
						<a class="nav-link" href="#" :class="{active:isActive=='done'}" @click.prevent="isActive='done'"></a>
					</li>
				</ul>
			</div>
			<ul class="list-group list-group-flush text-left">
				<div class="accordion" id="accordionExample">
					<div class="card" v-if="isActive=='all'">
						<div class="card-header" id="headingOne">
							<h5 class="mb-0">
								<button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
									<label class="" style="margin-left:1rem;color:black">
										
									</label>
								</button>
							</h5>
						</div>
						<div id="collapseOne" class="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
							<div class="card-deck-wrapper container-fluid ">
								<div class="card-deck container-fluid p-2">
									<div class="card">
									<img class="card-img-top" src="https://www.immd.gov.hk/publications/a_report_2013/images/chapter2/C01.jpg" alt="Card image cap">
									<div class="card-block">
										<h5 class="card-title">Step 1 </h5>
										<p style="font-family:" class="card-text"></p>
									</div>
									</div>
									<div class="card">
									<img class="card-img-top" src="http://m1.aboluowang.com/uploadfile/2015/0513/20150513062543427.jpeg" alt="Card image cap">
									<div class="card-block">
										<h5 class="card-title">Step 2 </h5>
										<p style="font-family:" class="card-text"> : ()</p>
									</div>
									</div>
									<div class="card">
									<img class="card-img-top" src="http://img.chinatimes.com/newsphoto/2018-01-31/656/20180131004774.jpg" alt="Card image cap">
									<div class="card-block">
										<h5 class="card-title">Step 3 </h5>
										<p style="font-family:" class="card-text">(Baggage Claim)</p>
									</div>
									</div>
									<div class="card">
									<img class="card-img-top" src="http://imgs.ntdtv.com/pic/2017/5-1/p8157021a136039129.jpg" alt="Card image cap">
									<div class="card-block">
										<h5 class="card-title">Step 4 </h5>
										<p style="font-family:" class="card-text"></p>
									</div>
									</div>
								</div>
								</div>
						</div>
					</div>
					<div class="card" v-if="isActive=='all'">
						<div class="card-header" id="headingTwo">
							<h5 class="mb-0 completed">
								<button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="true" aria-controls="collapseOne">
									<label class="" style="margin-left:1rem;color:black">
										
									</label>
								</button>
							</h5>
						</div>
						<div id="collapseTwo" class="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
							<div style="font-family:" class="card-body">
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 1</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 2</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 3</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 4</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 5</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-danger">Step 6</button> </div>
							</div>
						</div>
					</div>
					<div class="card" v-if="isActive=='all'">
						<div class="card-header" id="headingThree">
							<h5 class="mb-0">
								<button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseThree" aria-expanded="true" aria-controls="collapseOne">
									<label class="" style="margin-left:1rem;color:black">
										
									</label>
								</button>
							</h5>
						</div>
						<div id="collapseThree" class="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
							<div style="font-family:" class="card-body">
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 1</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 2</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 3</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 4</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 5</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-success">Step 6</button> </div>
							</div>
						</div>
					</div>
					<div class="card" v-if="isActive=='all'">
						<div class="card-header" id="headingThree">
							<h5 class="mb-0">
								<button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseFour" aria-expanded="true" aria-controls="collapseOne">
									<label class="" style="margin-left:1rem;color:black">
										
									</label>
								</button>
							</h5>
						</div>
						<div id="collapseFour" class="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
							<div style="font-family:" class="card-body">
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 1</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 2</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 3</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 4</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 5</button> </div>
								<div><button type="button" style="margin-bottom:10px" class="btn btn-primary">Step 6</button> </div>
							</div>
						</div>
					</div>
					<div class="card" v-for="(todo,i) in filterTask" :key="i">
						<div class="card-header" id="headingTwo">
							<h5 class="mb-0">
								<div class="form-check" >
									<input type="checkbox" v-model="todo.isCompleted" class="form-check-input" style="margin-top:0.8rem" >
								</div>
								<button class="btn btn-link" type="button" data-toggle="collapse" :data-target="`#collapse${i}`" aria-expanded="true" aria-controls="collapseOne">
									<label :class="{completed:todo.isCompleted}" style="margin-left:1rem;color:black">
										{{todo.Name}}
									</label>
								</button>
								<button type="button" class="close ml-auto" @click="deleteTodo(todo)" aria-label="Close">
									<span aria-hidden="true">&times;</span>
								</button>
							</h5>
						</div>
						<div :id="`collapse${i}`" class="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
							<div class="card-body">
								<h4 class="card-title font-weight-bold" v-if="todo.Add!=undefined" >Locate : {{todo.Add}}</h4>
								<p class="card-text" v-if="todo.message!=undefined">Message : {{todo.message}}</p>
								<footer class="blockquote-footer" v-if="todo.date!=undefined || todo.time!=undefined">  : {{todo.date}} {{todo.time}}</footer>
							</div>
						</div>
					</div>
				</div>
			</ul>
			<div class="card-footer d-flex justify-content-between">
				<span> {{undoneTask}} </span>
				<a href="#" @click="todos=[]"></a>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	name: "todolist",
	props:["todos","hotels","foods","attractions"],
	data(){
		return{
			newtodo:{
				Add:""
			},
			isInput:false,
			isActive:"all",
			idcode:0,
			choosable:[]
		}
	},
	methods:{
		addTodo:function(){
			if(this.newtodo.Name!=undefined){
				this.newtodo.icon="fas fa-check-circle text-info"
				this.todos.push(this.newtodo);
			}
			this.newtodo={};
			this.isInput=false;
		},
		deleteTodo:function(item){
			const vm=this;
			let num=vm.todos.findIndex((todo,i)=>{
				return todo.Name==item.Name
			})
			vm.todos.splice(num,1);
		}
	},
	computed:{
		undoneTask:function(){
			let number=0;
			this.todos.forEach((item)=>{
				if(item.isCompleted===false || item.isCompleted==undefined){
					number++;
				}
			})
			return number
		},
		filterTask:function(){
			const vm=this;
			let tempTodos=[]
			if(vm.isActive=='process'){
				vm.todos.forEach((item)=>{
					if(item.isCompleted==false || item.isCompleted==undefined){
						tempTodos.push(item);
					}
				})
			}else if(vm.isActive=='done'){
				vm.todos.forEach((item)=>{
					if(item.isCompleted==true){
						tempTodos.push(item);
					}
				})
			}else{
				tempTodos=this.todos;
			}
			return tempTodos
		}
	},
	created(){
		this.choosable=[...this.hotels,...this.foods,...this.attractions];
	}
}
</script>

<style>
.completed {
	text-decoration: line-through
}
</style>



